.container {
	height: 100vh;
	width: 100vw;
	background-color: beige;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-size: 1rem;
}

.header {
	margin: 2rem 0 1rem;
	font-size: 3em;
	display: flex;
	flex-wrap: wrap-reverse;
	align-items: center;
	justify-content: center;
}

.logo {
	height: 2em;
}

.main {
	margin-top: 1rem;
	height: 20rem;
	width: 40rem;
	border: 2px brown dashed;
	padding: 1rem 2rem 2rem 2rem;
	display: flex;
	flex-direction: column;
	position: relative;
}

.main.complete {
	animation: tada;
	animation-delay: 0.5s;
	animation-duration: 1s;
	animation-fill-mode: forwards;
}

.loader-container {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-30px, -30px);
}

.title-row {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 1rem;
	grid-gap: 0.25rem 0.5rem;
	font-size: 1.25em;
}

.title {
	font-weight: bold;
	text-decoration: underline;
	width: fit-content;
}

.stop-watch {
	width: fit-content;
	font-family: monospace;
}

.typing-container {
	position: relative;
	flex-grow: 1;
}

.typing-aria {
	position: absolute;
	width: calc(100% - 2rem);
	height: calc(100% - 2rem);
	background-color: transparent;

	resize: none;
	border: none;
	padding: 1rem;
	box-shadow: 0px 0px 5px 4px #888888;
	font-size: 1em;
	font-family: inherit;
}

.typing-aria:focus-visible {
	outline: none;
}

.placeholder {
	color: silver;
	background-color: #fffef0;
}

.actions-section {
	margin-top: 2rem;
	margin-bottom: 3rem;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: row wrap;
	grid-gap: 1rem;
}

.action-button {
	background-color: rgba(245, 222, 179, 0.637);
	outline: none;
	border: none;
	border-radius: 4px;
	font-family: inherit;
	font-size: 1em;
	padding: 0.25rem 0.5rem;
	box-shadow: 1px 1px 10px 0px #888888;
	transition: background-color 0.1s;
	cursor: pointer;
}
.action-button.second {
	background-color: aliceblue;
}

.action-button:hover {
	background-color: wheat;
}
.action-button.second:hover {
	background-color: rgb(189 209 226);
}

.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.modal-content-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: center;
	background-color: beige;
	padding: 1rem;
	min-width: 15rem;
	max-width: 30rem;
	height: 10rem;
	font-family: Georgia, 'Times New Roman', Times, serif;
}

.center-vertical {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.center-vertical > * {
	font-family: inherit;
	width: fit-content;
}

.modal-content-wrapper input {
	padding: 0.25rem;
}
.modal-content-wrapper input:focus-visible {
	outline: none;
}

.modal-content-wrapper button {
	margin-top: 1rem;
}

.modal-content-wrapper .time {
	margin-top: 0.5rem;
}

.modal-content-wrapper .time b {
	margin-right: 0.5rem;
}

.modal-content-wrapper .table {
	border-collapse: separate;
	border-spacing: 1rem 0;
}

.modal-content-wrapper .table td {
	max-width: 10rem;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

@media only screen and (max-width: 60rem) {
	.header {
		margin: 1rem 0 0.5rem;
		font-size: 2em;
	}

	.main {
		width: 75%;
		flex-grow: 1;
		padding: 1rem;
		margin-top: 0.5rem;
	}

	.actions-section {
		margin-top: 1rem;
	}
}

@media only screen and (max-width: 35rem) {
	.header {
		margin: 0.25rem 0 0.25rem;
	}

	.main {
		width: 50%;
		flex-grow: 1;
		margin-top: 0rem;
	}

	.actions-section {
		margin-top: 0.75rem;
	}
}
